/* AdminTabs.css */
.admin-tabs {
    margin: 20px;
  }
  .admin-tabs button {
    background-color: #3498DB;
    color: white;
    border: none;
    padding: 10px 15px;
    margin-right: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  .admin-tabs button:hover {
    background-color: #2980B9;
  }
  .tab-content {
    margin-top: 20px;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 10px;
  }
  