/* Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    background-color: #2C3E50;
    padding: 15px;
  }
  .nav-links {
    display: flex;
    list-style-type: none;
  }
  .nav-links li {
    margin: 0 15px;
  }
  .nav-links a {
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  .nav-links a:hover {
    color: #18BC9C;
  }
  