/* Modal.css */

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure it's on top of other content */
  }
  
  .modal-content {
    background: white;
    border-radius: 8px;
    padding: 20px;
    max-width: 90%; /* Responsive width */
    max-height: 90%; /* Responsive height */
    overflow: hidden; /* Prevent content overflow */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    position: relative;
  }
  
  .modal-image {
    width: 100%; /* Image takes full width of modal */
    height: auto; /* Maintain aspect ratio */
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: red;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  